/**
* WebNav.js
* Navigation for Web view
*
* @version 0.0.1
* @author [Nicolette Chan](nicolette@policypal.com)
*/
import $ from 'jquery';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { withRouter /* , Link */ } from 'react-router-dom';
import {
  Row,
  Col,
  Container,
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  // NavLink,
} from 'reactstrap';
// import logo
import PPLogo from 'assets/images/common/PolicyPalLogo.png';
// import nav category icons
import CategoriesIcon from 'assets/images/NavIcons';
import Female from 'assets/images/Profile/FemaleSmall.png';
import Male from 'assets/images/Profile/MaleSmall.png';
// import logo
import Get5 from 'assets/images/NavIcons/get5.svg';

import { numberWithCommas } from 'lib/helperFunctions';

import PhoneButton from '../Templates/FormLanding/PhoneButton';

// import button
import Button from '../FormsV3/Button/Button';
import { navBg } from '../../actions/navActions';
import { resetInsuranceForm } from '../../actions/insuranceActions';
import { clearLoginData } from '../../actions/userActions';
import SpeechScroll from '../Templates/SpeechScroll/SpeechScroll';
import SpeechPopUp from '../Templates/SpeechPopUp/SpeechPopUp';
// import css
import './Navigation.scss';

class WebNav extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.navBG !== prevState.currentNavBG) {
      return ({
        currentNavBG: nextProps.navBG,
        currentNavCol: nextProps.navColors,
      });
    }
    return null;
  }
  constructor(props) {
    super(props);
    this.state = {
      currentNavBG: '',
      isScrolled: false,
    };
    // render more tab
    this.moreTab = [
      { name: 'Claims', url: '/claims' },
      { name: 'Partnership', url: '/partnership' },
      { name: 'About Us', url: '/about' },
      { name: 'Career', url: '/career' },
      { name: 'FAQ', url: '/faq' },
    ];
    this.clickLogo = this.clickLogo.bind(this);
    this.renderHoverIn = this.renderHoverIn.bind(this);
    this.renderHoverOut = this.renderHoverOut.bind(this);
    this.renderFull = this.renderFull.bind(this);
    this.renderCategory = this.renderCategory.bind(this);
    this.onScroll = this.onScroll.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.onScroll);
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onScroll);
  }

  onScroll() {
    const { isScrolled } = this.state;
    const isNowScrolled = window.scrollY > 0;
    if (isNowScrolled !== isScrolled) {
      this.setState({
        isScrolled: isNowScrolled,
      });
    }
  }

  setNavRef(node) {
    this.navRef = node;
  }

  isSignedIn() {
    const {
      login,
      userDetails,
      isCategory,
      isBusiness,
      getQuoteCallback,
    } = this.props;
    // user signed in
    const {
      speechScrollProfile,
      isOnHoverProfile,
    } = this.state;


    if (isBusiness) {
      if (isCategory) {
        return null;
      }
      return (
        <div className="nav-item business-button">
          <Button
            variant="small-light-green"
            width={105}
            height={32}
            onClick={() => { if (getQuoteCallback) { getQuoteCallback(); } }}
          >
            <p>Get Quote</p>
          </Button>
        </div>
      );
    }

    const hoverProfile = !isOnHoverProfile ? 'green-font-col' : 'green-hover-col';
    const isLoggedIn = userDetails !== null;
    const profilePic = isLoggedIn && userDetails.gender && userDetails.gender.toUpperCase() === 'FEMALE' ? Female : Male;
    let surName = isLoggedIn ? userDetails.surname : null;
    surName = surName || 'User';

    const signingStyle = isCategory ? 'category-style' : '';
    const signUpVariant = isCategory ? 'medium-flat-light-green' : 'small-flat-dark';
    const signUpText = isCategory ? 'Register' : 'Sign Up';

    return (
      <Fragment>
        <ul
          className="nav-ul user-ul user-nav"
          style={{ display: isLoggedIn ? 'block' : 'none' }}
          ref={node => (this.loginRef = node)}
        >
          <div className="green-font-col nav-link">
            <div className="profile-pic">
              <img src={profilePic} alt="profile" />
            </div>
            <div
              className="user-name"
              onMouseMove={(event) => {
                speechScrollProfile.handleInsurerHover(event, () => this.renderHoverIn('profile'), () => this.renderHoverOut('profile'));
              }}
            >
              <p className={hoverProfile}>{surName}</p>
            </div>
          </div>
        </ul>
        <div className={signingStyle} style={{ display: isLoggedIn ? 'none' : 'block' }}>
          <Button
            className="nav-item"
            variant="small-outline"
            width={97}
            height={36}
            onClick={() => { login.popup.openPopup(login.loginContainer.setInitialIndex(1)); }}
          >
            <p>Sign In</p>
          </Button>
          <Button
            className="nav-item"
            variant={signUpVariant}
            width={97}
            height={36}
            onClick={() => { login.popup.openPopup(login.loginContainer.setInitialIndex(2)); }}
          >
            <p>{signUpText}</p>
          </Button>
        </div>
      </Fragment>
    );
  }
  clickLogo() {
    // on Homepage
    // scroll to top when clicked on PolicyPal Logo
    const { history } = this.props;
    const button = $('#dot-first-button');

    if (button) {
      $(button).trigger('click');
    }
    // if not on Homepage
    // go to Homepage
    history.push('/');
  }
  renderInsuranceLinks() {
    const { speechScrollInsurance } = this.state;
    const { history } = this.props;
    const category = CategoriesIcon.color;
    const renderLinks = _.map(category, (link, index) => {
      const fontSizeStyle = link.name.length > 10 ? '14px' : '16px';
      return (
        <Col key={`${link}-${index}`} xs={6}>
          <button
            className="nav-drop-button"
            onClick={(e) => {
              this.props.resetInsuranceForm();
              history.push(link.url);
              speechScrollInsurance.handleInsurerBlur(e);
            }}
            key={`insurer-link-${index}`}
          >
            <span
              className="nav-font"
              style={{ fontSize: fontSizeStyle }}
            >
              <img
                src={link.src}
                className="nav-icon"
                alt={`icon-${index}`}
              />
              {link.name}
            </span>
          </button>
        </Col>
      );
    });
    return <Row noGutters>{renderLinks}</Row>;
  }
  renderMoreLinks() {
    const { speechScrollMore } = this.state;
    const { history } = this.props;
    const renderMoreLinks = _.map(this.moreTab, (link, index) => (
      <button
        className="nav-drop-button"
        onClick={(e) => {
          history.push(link.url);
          speechScrollMore.handleInsurerBlur(e);
        }}
        key={`more-link-${index}`}
      >
        <span className="nav-font">
          {link.name}
        </span>
      </button>
    ));
    return renderMoreLinks;
  }
  renderHoverIn(desc) {
    switch (desc) {
      case 'insurance':
        return (
          this.setState({
            isOnHoverInsurance: true,
          })
        );
      case 'more':
        return (
          this.setState({
            isOnHoverMore: true,
          })
        );
      case 'profile':
        return (
          this.setState({
            isOnHoverProfile: true,
          })
        );
      default:
        return (
          this.setState({
            isOnHoverInsurance: false,
            isOnHoverMore: false,
            isOnHoverProfile: false,
          })
        );
    }
  }
  renderHoverOut(desc) {
    switch (desc) {
      case 'insurance':
        return (
          this.setState({
            isOnHoverInsurance: false,
          })
        );
      case 'more':
        return (
          this.setState({
            isOnHoverMore: false,
          })
        );
      case 'profile':
        return (
          this.setState({
            isOnHoverProfile: false,
          })
        );
      default:
        return (
          this.setState({
            isOnHoverInsurance: false,
            isOnHoverMore: false,
            isOnHoverProfile: false,
          })
        );
    }
  }

  renderFull() {
    const {
      speechScrollMore,
      speechScrollInsurance,
      isOnHoverInsurance,
      isOnHoverMore,
    } = this.state;

    const {
      navColors,
      onPageInsurance,
      onPageMore,
      // onPageBusiness,
      history,
      isBusiness,
    } = this.props;

    const hoverInsurance = !isOnHoverInsurance ? 'green-font-col' : 'green-hover-col';
    const hoverMore = !isOnHoverMore ? 'green-font-col' : 'green-hover-col';

    return (
      <div>
        <Collapse isOpen={true} navbar>
          <Nav
            className="ml-auto"
            navbar
          >
            <Nav className="nav-main-links">
              <ul className="business-toggle">
                <Button
                  variant={isBusiness ? 'medium-outline' : 'medium-outline'}
                  height={32}
                  width={110}
                  onClick={() => {
                    history.push(isBusiness ? '/' : '/business');
                  }}
                >
                  <p>{isBusiness ? 'Personal' : 'Business'}</p>
                </Button>
              </ul>
              <ul className={`nav-ul ${onPageInsurance} insurance-nav`}>
                <SpeechPopUp
                  onHoverButtonClick={() => history.push('/categories')}
                  ref={node => !this.state.speechScrollInsurance &&
                    this.setState({ speechScrollInsurance: node })}
                >
                  {this.renderInsuranceLinks()}
                </SpeechPopUp>
                <div className="green-font-col nav-link">
                  <span
                    onMouseMove={(event) => {
                      speechScrollInsurance.handleInsurerHover(event, () => this.renderHoverIn('insurance'), () => this.renderHoverOut('insurance'));
                    }}
                    className={hoverInsurance}
                  >
                    Buy Policy
                  </span>
                  <div className="page-indicator" />
                </div>
              </ul>
              {/* <ul className={`nav-ul ${onPageBusiness}`}>
                <NavLink tag={Link} to="/marketplace" className={navColors}>Marketplace</NavLink>
                <div className="page-indicator" />
              </ul> */}
              <ul className={`nav-ul ${onPageMore} more-nav`} style={{ width: '80px' }}>
                <SpeechScroll
                  maxHeight={350}
                  ref={node => !this.state.speechScrollMore &&
                    this.setState({ speechScrollMore: node })}
                >
                  {this.renderMoreLinks()}
                </SpeechScroll>
                <div className="green-font-col nav-link">
                  <span
                    onMouseMove={(event) => {
                      speechScrollMore.handleInsurerHover(event, () => this.renderHoverIn('more'), () => this.renderHoverOut('more'));
                    }}
                    className={hoverMore}
                  >
                    More
                  </span>
                  <div className="page-indicator" />
                </div>
              </ul>
              <NavItem className="nav-ul blog-a">
                <a href="https://blog.policypal.com" className={`nav-link ${navColors}`}>Blog</a>
              </NavItem>
              {this.isSignedIn()}
            </Nav>
          </Nav>
        </Collapse>
      </div>
    );
  }

  renderCategory() {
    const { quoteData } = this.props;

    let finalQuote = null;
    let isBalance = false;

    try {
      // const isCustomize = formState && formState.path === '/customize';
      const isDeposit = quoteData && quoteData.isDeposit;
      isBalance = quoteData && quoteData.isBalance;
      const quote = quoteData && quoteData.quote && quoteData.quote.quote;
      // eslint-disable-next-line
      finalQuote = quote && (isBalance ? quote.outstanding_premium : isDeposit ? quote.deposit_premium : quote.final_premium);
    } catch (error) {
      // eslint-disable-next-line
    }

    return (
      <div>
        <Collapse isOpen={true} navbar>
          <Nav
            className="ml-auto"
            navbar
          >
            <Nav className="nav-main-links">
              {
                finalQuote &&
                <div className="sticky-top-price">
                  <p>{isBalance ? 'Balance' : 'Total price'}</p>
                  <p>${numberWithCommas(finalQuote)}</p>
                </div>
              }
              <PhoneButton />
              {this.isSignedIn()}
            </Nav>
          </Nav>
        </Collapse>
      </div>
    );
  }

  render() {
    // navigation colors
    const {
      currentNavBG,
      speechScrollProfile,
      isScrolled,
    } = this.state;
    const {
      history,
      isCategory,
      isMain,
      logout,
      navColors,
    } = this.props;

    let navStyle = currentNavBG;
    let scrolledStyle = '';

    if (isCategory) {
      navStyle = isMain ? 'category-main' : 'category-sub';

      if (isScrolled || !isMain) {
        scrolledStyle = 'category-opaque';
      }
    } else if (isScrolled && navStyle === navBg.WHITE) {
      scrolledStyle = 'nav-white-bg-opaque';
    }

    return (
      <div className="navigation d-sm-none d-none d-md-none d-lg-block d-xl-block">
        <Navbar className={`${navStyle} ${scrolledStyle}`} fixed="top" expand="md">
          <Container fluid>
            <NavbarBrand>
              <div className="brand-container">
                <button
                  onClick={() => {
                    this.clickLogo();
                  }}
                >
                  <img src={PPLogo} alt="PolicyPal Logo" />
                </button>
                <button
                  onClick={() => history.push('/rewards')}
                  style={{ marginLeft: '20px' }}
                >
                  <img src={Get5} alt="get5.svg" />
                </button>
              </div>
            </NavbarBrand>
            <NavbarToggler />
            <div className={isCategory ? 'd-block' : 'd-none'}>
              {this.renderCategory()}
            </div>
            <div className={isCategory ? 'd-none' : 'd-block'}>
              {this.renderFull()}
            </div>
            {/* {
              isCategory ?
                this.renderCategory()
              :
                this.renderFull()
            } */}
            <div className="account-speech-scroll">
              <SpeechScroll
                maxHeight={350}
                ref={node => !this.state.speechScrollProfile &&
                  this.setState({ speechScrollProfile: node })}
              >
                <button
                  className={navColors}
                  onClick={(e) => {
                    this.props.resetInsuranceForm();
                    history.push('/profile');
                    speechScrollProfile.handleInsurerBlur(e);
                  }}
                />
                <div>
                  <button
                    className="nav-drop-button"
                    onClick={() => {
                      history.push('/profile');
                    }}
                  >
                    <span className="nav-font">
                      My Account
                    </span>
                  </button>
                  <button
                    className="nav-drop-button"
                    onClick={() => {
                      logout();
                      window.location.href = '/';
                    }}
                  >
                    <span className="nav-font">
                      Log Out
                    </span>
                  </button>
                </div>
              </SpeechScroll>
            </div>
          </Container>
        </Navbar>
      </div>
    );
  }
}
WebNav.defaultProps = {
  onPageInsurance: '',
  onPageMore: '',
  // onPageBusiness: '',
  login: null,
  userDetails: null,
  getQuoteCallback: null,
  quoteData: null,
  // formState: null,
};
WebNav.propTypes = {
  navColors: PropTypes.string.isRequired,
  onPageInsurance: PropTypes.string,
  onPageMore: PropTypes.string,
  // onPageBusiness: PropTypes.string,
  login: PropTypes.object,
  userDetails: PropTypes.object,
  resetInsuranceForm: PropTypes.func.isRequired,
  logout: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  isCategory: PropTypes.bool.isRequired,
  isMain: PropTypes.bool.isRequired,
  isBusiness: PropTypes.bool.isRequired,
  getQuoteCallback: PropTypes.func,
  quoteData: PropTypes.object,
  // formState: PropTypes.oneOfType([PropTypes.number, PropTypes.object]),
};

const mapStateToProps = state => ({
  isCategory: state.nav.isCategory,
  isMain: state.nav.isMain,
  isBusiness: state.nav.isBusiness,
  getQuoteCallback: state.business.getQuoteCallback,
  quoteData: state.business.quoteData,
  formState: state.insurance.formState,
});

const mapDispatchToProps = dispatch => ({
  resetInsuranceForm: () => (
    dispatch(resetInsuranceForm())
  ),
  logout: () => (
    dispatch(clearLoginData())
  ),
});
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(WebNav));
