/**
* NavContainer.js
* Navigation container to determine if
* if navigation is web or mobile view
*
* @version 0.0.1
* @author [Nicolette Chan](nicolette@policypal.com)
*/
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import WebNav from '../components/Navigations/WebNav';
import MobileNav from '../components/Navigations/MobileNav';

import {
  navColors,
  navBg,
  onPageInsurance,
  onPageMore,
  onPageBusiness,
  // loginState,
} from '../actions/navActions';

class NavContainer extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    if (nextProps.navColors !== prevState.currColor
        || nextProps.navBg !== prevState.navBg
        || nextProps.onPageInsurance !== prevState.onPageInsurance
        || nextProps.onPageMore !== prevState.onPageMore
        || nextProps.onPageBusiness !== prevState.onPageBusiness
        || nextProps.userDetails.data !== prevState.userDetails.data
    ) {
      return ({
        currColor: nextProps.navColors,
        currBGColor: nextProps.navBg,
        currPageInsurance: nextProps.onPageInsurance,
        currPageMore: nextProps.onPageMore,
        currPageBusiness: nextProps.onPageBusiness,
        userDetails: nextProps.userDetails.data,
      });
    }
    return null;
  }
  constructor(props) {
    super(props);
    const userDetails = this.props;
    this.state = {
      currColor: navColors,
      currBGColor: navBg,
      currPageInsurance: onPageInsurance,
      currPageMore: onPageMore,
      currPageBusiness: onPageBusiness,
      currLoginState: true,
    };
    if (userDetails && userDetails.data) {
      this.state = {
        userDetails,
      };
    }
  }

  render() {
    const {
      currColor,
      currBGColor,
      currPageInsurance,
      currPageMore,
      currPageBusiness,
      currLoginState,
      userDetails,
    } = this.state;

    const { login } = this.props;

    return (
      <Fragment>
        <MobileNav
          navBG="nav-white-bg"
          loginState={currLoginState}
          userDetails={userDetails}
          login={login}
        />
        <WebNav
          navColors={currColor}
          navBG={currBGColor}
          onPageInsurance={currPageInsurance}
          onPageMore={currPageMore}
          onPageBusiness={currPageBusiness}
          loginState={currLoginState}
          userDetails={userDetails}
          login={login}
        />
      </Fragment>
    );
  }
}

NavContainer.propTypes = {
  login: PropTypes.object.isRequired,
};

const mapStateToProps = state => ({
  navColors: state.nav.navColors,
  currIndex: state.nav.currIndex,
  navBg: state.nav.navBg,
  onPageInsurance: state.nav.onPageInsurance,
  onPageMore: state.nav.onPageMore,
  onPageBusiness: state.nav.onPageBusiness,
  login: state.login,
  userDetails: state.userDetails,
});

export default connect(mapStateToProps, null)(NavContainer);
