/**
* MobileNav.js
* Navigation for Mobile view
*
* @version 0.0.1
* @author [Nicolette Chan](nicolette@policypal.com)
*/
import document from 'global/document';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import {
  Container,
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  NavLink,
  DropdownToggle,
  NavItem,
  DropdownItem,
  UncontrolledDropdown,
} from 'reactstrap';
import Rodal from 'rodal';

// include styles
import 'rodal/lib/rodal.css';

// import NavIcons
// hamburger
import MobileHamburger from 'assets/images/NavIcons/mobile-hamburger.svg';
// arrow
import Arrow from 'assets/images/forms/arrow.svg';

// import logo
import PPLogo from 'assets/images/common/PolicyPalLogo.png';
import PPLogoWhite from 'assets/images/common/policypal-logo-white.png';

// import nav category icons
import CategoriesIcon from 'assets/images/NavIcons';

import Female from 'assets/images/Profile/FemaleSmall.png';
import Male from 'assets/images/Profile/MaleSmall.png';

// import button
import Button from 'components/FormsV3/Button/Button';
import GoToApp from './GoToApp';
import { resetInsuranceForm } from '../../actions/insuranceActions';
import { clearLoginData } from '../../actions/userActions';

// import css
import './Navigation.scss';

class MobileNav extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false,
      collapseInsurance: false,
      collapseMore: false,
      rotateInsuranceArrow: true,
      rotateMoreArrow: true,
      visible: false,
    };
    // Rodal
    this.show = this.show.bind(this);
    this.hide = this.hide.bind(this);
    this.closeMenu = this.closeMenu.bind(this);
    this.toggle = this.toggle.bind(this);
    this.toggleSubInsurance = this.toggleSubInsurance.bind(this);
    this.toggleSubMore = this.toggleSubMore.bind(this);
    this.toggleSubProfile = this.toggleSubProfile.bind(this);

    // render more tab
    this.moreTab = [
      { name: 'Claims', url: '/claims' },
      { name: 'Partnership', url: '/partnership' },
      { name: 'Rewards', url: '/rewards' },
      { name: 'About Us', url: '/about' },
      { name: 'Career', url: '/career' },
      { name: 'FAQ', url: '/faq' },
      { name: 'Blog', url: 'https://blog.policypal.com/', type: 'external' },
    ];
  }
  show() {
    const el = document.getElementsByTagName('body')[0];
    el.classList.add('noscroll');
    this.setState({ visible: true });
  }

  hide() {
    const el = document.getElementsByTagName('body')[0];
    el.classList.remove('noscroll');
    this.setState({ visible: false });
  }

  // closes all menu
  closeMenu() {
    const el = document.getElementsByTagName('body')[0];
    el.classList.remove('noscroll');
    this.setState({
      visible: false,
      modal: false,
      collapseInsurance: false,
      collapseMore: false,
      collapseProfile: false,
      rotateInsuranceArrow: true,
      rotateMoreArrow: true,
      rotateProfileArrow: false,
    });
  }
  // toggle main mobile menu
  toggle() {
    this.setState({
      modal: !this.state.modal,
    });
  }
  // toggle for Insurance dropdown
  toggleSubInsurance() {
    this.setState({
      collapseInsurance: !this.state.collapseInsurance,
      rotateInsuranceArrow: !this.state.rotateInsuranceArrow,
      collapseMore: false,
      rotateMoreArrow: true,
      collapseProfile: false,
      rotateProfileArrow: false,
    });
  }
  // toggle for More dropdown
  toggleSubMore() {
    this.setState({
      collapseInsurance: false,
      rotateInsuranceArrow: true,
      collapseMore: !this.state.collapseMore,
      rotateMoreArrow: !this.state.rotateMoreArrow,
      collapseProfile: false,
      rotateProfileArrow: false,
    });
  }
  // toggle for More + dropdown
  toggleSubProfile() {
    this.setState({
      collapseInsurance: false,
      rotateInsuranceArrow: true,
      collapseMore: false,
      rotateMoreArrow: true,
      collapseProfile: !this.state.collapseProfile,
      rotateProfileArrow: !this.state.rotateProfileArrow,
    });
  }
  renderInsuranceLink() {
    const category = CategoriesIcon.white;
    const renderLinks = _.map(category, (link, index) => (
      <NavLink
        key={`insur-${index}`}
        tag={Link}
        to={link.url}
        onClick={this.closeMenu}
      >
        <DropdownItem key={index}>
          <span className="nav-font">
            <img
              src={link.src}
              className="nav-icon"
              alt={`icon-${index}`}
            />
            {link.name}
          </span>
        </DropdownItem>
      </NavLink>
    ));
    return renderLinks;
  }
  renderMoreLinks() {
    const renderMoreLinks = _.map(this.moreTab, (link, index) => {
      if (link && link.type && link.type === 'external') {
        return (
          <a
            className="nav-link"
            target="__blank"
            href={link.url}
            key={`insur-more-${index}`}
            onClick={() => {
              this.closeMenu();
              this.props.resetInsuranceForm();
            }}
          >
            <DropdownItem key={index}>
              <span className="nav-font">
                {link.name}
              </span>
            </DropdownItem>
          </a>
        );
      }

      return (
        <NavLink
          key={`insur-more-${index}`}
          tag={Link}
          to={link.url}
          onClick={() => {
            this.closeMenu();
            this.props.resetInsuranceForm();
          }}
        >
          <DropdownItem key={index}>
            <span className="nav-font">
              {link.name}
            </span>
          </DropdownItem>
        </NavLink>
      );
    });
    return renderMoreLinks;
  }
  renderSignIn() {
    const { userDetails, login, logout } = this.props;
    const rotateProfileArrow = this.state.rotateProfileArrow ? 'turn' : 'arrow';

    if (userDetails !== null) {
      const profilePic = userDetails.gender && userDetails.gender.toUpperCase() === 'FEMALE' ? Female : Male;
      const surName = userDetails.surname || 'User';

      return (
        <Fragment>
          <UncontrolledDropdown nav inNavbar>
            <DropdownToggle nav onClick={this.toggleSubProfile}>
              <div className="profile-pic">
                <img src={profilePic} alt="profile" />
              </div>
              <div className="user-name">
                <p>{surName}</p>
              </div>
              <div className="arrow-container">
                <img src={Arrow} alt="/" className={rotateProfileArrow} />
              </div>
            </DropdownToggle>
            <Collapse className="sub-nav-menu" isOpen={this.state.collapseProfile}>
              <NavLink tag={Link} to="/profile" onClick={this.closeMenu} >
                <DropdownItem>
                  <span className="nav-font">
                    My Account
                  </span>
                </DropdownItem>
              </NavLink>
              <NavLink
                tag={Link}
                to="/"
                onClick={() => {
                  logout();
                  this.closeMenu();
                }}
              >
                <DropdownItem>
                  <span className="nav-font">
                    Log Out
                  </span>
                </DropdownItem>
              </NavLink>
            </Collapse>
          </UncontrolledDropdown>
        </Fragment>
      );
    }

    return (
      <div className="button-container">
        {/* Sign In | Sign Up Buttons */}
        <Button
          variant="small-outline"
          width={140}
          height={60}
          onClick={() => { login.popup.openPopup(login.loginContainer.setInitialIndex(1)); }}
        >
          <p>Sign In</p>
        </Button>
        <Button
          variant="small-flat"
          width={140}
          height={60}
          onClick={() => { login.popup.openPopup(login.loginContainer.setInitialIndex(2)); }}
        >
          <p>Sign Up</p>
        </Button>
      </div>
    );
  }
  render() {
    const rotateInsuranceArrow = this.state.rotateInsuranceArrow ? 'arrow' : 'turn';
    const rotateMoreArrow = this.state.rotateMoreArrow ? 'arrow' : 'turn';

    const {
      navBG,
      navColors,
    } = this.props;

    return (
      <div className="navigation d-md-block d-lg-none d-xl-none">
        <Navbar fixed="top" expand="lg" className={`mobile-nav ${navBG}`}>
          <Container fluid>
            <NavbarBrand
              tag={Link}
              to="/"
              onClick={() => {
                window.scrollTo(0, 0);
              }}
            >
              <img src={PPLogo} alt="PolicyPal Logo" />
            </NavbarBrand>
            <NavbarToggler onClick={this.show}>
              <img src={MobileHamburger} alt="" />
            </NavbarToggler>
            <Rodal
              className="nav-mobile-opened nav-mobile-opened-content"
              visible={this.state.visible}
              onClose={this.hide}
              animation="door"
            >
              <Fragment>
                <NavLink
                  tag={Link}
                  to="/"
                  className="PP-logo navbar-brand"
                  onClick={this.closeMenu}
                >
                  <img src={PPLogoWhite} alt="PolicyPal Logo" />
                </NavLink>
                {this.renderSignIn()}
                <NavItem>
                  <NavLink tag={Link} to="/referral" onClick={this.closeMenu}>Get $5</NavLink>
                </NavItem>
                {/* <NavItem>
                  <NavLink tag={Link} to="/marketplace" onClick={this.closeMenu}>Marketplace</NavLink>
                </NavItem> */}
                {/* Insurance Dropdown */}
                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav onClick={this.toggleSubInsurance}>
                    Buy Policy
                    <div className="arrow-container">
                      <img src={Arrow} alt="/" className={rotateInsuranceArrow} />
                    </div>
                  </DropdownToggle>
                  <Collapse className="sub-nav-menu" isOpen={this.state.collapseInsurance}>
                    {this.renderInsuranceLink()}
                  </Collapse>
                </UncontrolledDropdown>
                <UncontrolledDropdown nav inNavbar>
                  <DropdownToggle nav onClick={this.toggleSubMore} className={navColors} >
                    More
                    <div className="arrow-container">
                      <img src={Arrow} alt="/" className={rotateMoreArrow} />
                    </div>
                  </DropdownToggle>
                  <Collapse className="sub-nav-menu" isOpen={this.state.collapseMore}>
                    {this.renderMoreLinks()}
                  </Collapse>
                </UncontrolledDropdown>
                {/* Business */}
                <NavItem>
                  <NavLink tag={Link} to="/business" onClick={this.closeMenu}>Business</NavLink>
                </NavItem>
                {/* Blog */}
                <NavItem>
                  <a href="https://blog.policypal.com" className={`nav-link ${navColors}`}>Blog</a>
                </NavItem>
              </Fragment>
            </Rodal>
          </Container>
        </Navbar>
        <GoToApp />
      </div>
    );
  }
}

MobileNav.defaultProps = {
  navBG: null,
  navColors: null,
  login: null,
  userDetails: null,
};

MobileNav.propTypes = {
  resetInsuranceForm: PropTypes.func.isRequired,
};

MobileNav.propTypes = {
  navBG: PropTypes.string,
  navColors: PropTypes.string,
  login: PropTypes.object,
  userDetails: PropTypes.object,
  logout: PropTypes.func.isRequired,
};

const mapDispatchToProps = dispatch => ({
  resetInsuranceForm: () => (
    dispatch(resetInsuranceForm())
  ),
  logout: () => (
    dispatch(clearLoginData())
  ),
});

export default connect(null, mapDispatchToProps)(MobileNav);
